.bg-weather{
    background: url('https://dthezntil550i.cloudfront.net/db/0002552711/3adee43a-ceb3-4381-83c2-3a0e63daecc6.png') no-repeat center center/cover;

}
.bg-weather-app{
    background: rgba(144, 132, 141, 0.7);
}
.ul-weather{
    list-style: none;
}
.ul-weather li{
    list-style: none;
    border-style: solid;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom-color: #52515e;

    border-width: thin;
}





.loader1,
.loader1:after {
    border-radius: 50%;
    width: 10em;
    height: 10em;
}
.loader1 {
    margin: 60px auto;
    font-size: 10px;
    position: relative;
    text-indent: -9999em;
    border-top: 1.1em solid rgba(255, 255, 255, 0.2);
    border-right: 1.1em solid rgba(255, 255, 255, 0.2);
    border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
    border-left: 1.1em solid #ffffff;
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-animation: load8 1.1s infinite linear;
    animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes load8 {
    0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}